import React, { Component } from 'react'

class Manettes extends Component {

  render() {

    const {className} = this.props

    return (

      <svg xmlns="http://www.w3.org/2000/svg" className={ (className) ? className : '' } style={{fillRule: 'evenodd'}} width="295" height="391" viewBox="0 0 295 391">
      <image id="Manettes" width="295" height="391" xlinkHref="data:img/png;base64,"/>
    </svg>


  )

}

}

export default Manettes;



