import React, { Component } from 'react'

class MegaMan extends Component {

  render() {

    const {className} = this.props

    return (

      <svg xmlns="http://www.w3.org/2000/svg" className={ (className) ? className : '' } style={{fillRule: 'evenodd'}} width="304" height="315" viewBox="0 0 304 315">
      <image id="Mega_man" data-name="Mega man" width="304" height="315" xlinkHref="data:img/png;base64,"/>
    </svg>


  )

}

}

export default MegaMan;



