import React, { Component } from 'react'

class ASport extends Component {

  render() {

    const {className} = this.props

    return (

      <svg xmlns="http://www.w3.org/2000/svg" className={ (className) ? className : '' } style={{fillRule: 'evenodd'}} width="130" height="103" viewBox="0 0 130 103">
      <image id="Un-sport-OUI" width="130" height="103" xlinkHref="data:img/png;base64,"/>
    </svg>


  )

}

}

export default ASport;



