              <a href="https://www.riftherald.com/lol-worlds/2017/12/19/16797364/league-of-legends-worlds-viewers-statistics">https://www.riftherald.com/lol-worlds/2017/12/19/16797364/league-of-legends-worlds-viewers-statistics</a>
