import React, { Component } from 'react'

class Pacman extends Component {

  render() {

    const {className} = this.props

    return (

      <svg xmlns="http://www.w3.org/2000/svg" className={ (className) ? className : '' } style={{fillRule: 'evenodd'}} width="344" height="218" viewBox="0 0 344 218">
      <image id="Pacman" width="344" height="218" xlinkHref="data:img/png;base64,"/>
    </svg>


  )

}

}

export default Pacman;



